import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { cn } from '@/lib/utils';
import { carsTypes } from '../../data/configs.data';
import TextInputForm from '@/components/forms/TextInputForm';

export default function TechnicalSpecs() {
    const { control, watch } = useFormContext();
    const values = watch();

    return (
        <div className="mt-6">
            <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
                <FormField
                    control={control}
                    name="car_type"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                نوع السيارة
                            </FormLabel>
                            <Select onValueChange={field.onChange} defaultValue={field.value} value={field.value}>
                                <FormControl>
                                    <SelectTrigger>
                                        <SelectValue
                                            placeholder={<span style={{ color: '#D1D1D1' }}>أدخل نوع السيارة</span>}
                                        />
                                    </SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    {carsTypes.map((type) => (
                                        <SelectItem key={type} value={type}>
                                            {type}
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            <FormMessage />
                        </FormItem>
                    )}
                />

                <FormField
                    control={control}
                    name="transmission_type"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                نوع ناقل الحركة
                            </FormLabel>
                            <FormControl>
                                <RadioGroup
                                    onValueChange={field.onChange}
                                    value={field.value}
                                    className="flex justify-end gap-6"
                                >
                                    <div className="flex items-center gap-2 rounded-lg ps-[74px] pe-[9px] py-[6px] border border-[#E8E7EA]">
                                        <label
                                            className={cn(
                                                'font-medium text-4 leading-5 tracking-normal text-start text-white-blue-900',
                                                { 'text-primery-blue-600': values.transmission === 'automatic' },
                                            )}
                                            htmlFor="automatic"
                                        >
                                            أتوماتيك
                                        </label>
                                        <RadioGroupItem value="auto" id="automatic" />
                                    </div>
                                    <div className="flex items-center gap-2 rounded-lg ps-[90px] pe-[9px] py-[6px] border border-[#E8E7EA]">
                                        <label
                                            className={cn(
                                                'font-medium text-4 leading-5 tracking-normal text-start text-white-blue-900',
                                                { 'text-primery-blue-600': values.transmission === 'manual' },
                                            )}
                                            htmlFor="manual"
                                        >
                                            يدوي
                                        </label>
                                        <RadioGroupItem value="manual" id="manual" />
                                    </div>
                                </RadioGroup>
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />

                <FormField
                    control={control}
                    name="vin"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                رقم الهيكل
                            </FormLabel>
                            <FormControl>
                                <Input
                                    {...field}
                                    className="placeholder:text-white-blue-600"
                                    placeholder="أدخل رقم الهيكل"
                                />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <TextInputForm name="tank" type="number" label="سعة خزان الوقود" placeholder="أدخل سعة خزان الوقود" />
                <TextInputForm name="horse_power" type="number" label="طاقة المحرك" placeholder="أدخل طاقة المحرك" />
                <TextInputForm
                    name="mileage"
                    type="number"
                    label="عدد الكيلومتر الحالي"
                    placeholder="أدخل عدد الكيلومتر الحالي"
                />
            </div>
        </div>
    );
}
