import { FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { InputOTP, InputOTPGroup, InputOTPSlot } from '@/components/ui/input-otp';
import { useFormContext } from 'react-hook-form';

const VehicleNumber = () => {
    const { control } = useFormContext();
    return (
        <div className="rounded-lg p-2 text-primery-blue-900">
            <div className="flex flex-row items-center justify-between flex-wrap">
                {/* Arabic Letters/Numbers OTP */}
                <div className="flex flex-row items-center">
                    <h2 className="font-medium pe-1 text-nowrap mx-2 text-[18px] leading-[33.73px] tracking-normal text-start">
                        رقم اللوحة
                    </h2>
                    <FormField
                        control={control}
                        name="arabicOtp"
                        render={({ field }) => (
                            <FormItem className="flex flex-col items-center justify-center p-2 rounded-lg border border-secondary-blue-400">
                                <FormLabel className="font-medium mx-2 text-[18px] leading-[33.73px] tracking-normal text-start">
                                    الحروف العربية
                                </FormLabel>
                                <FormControl>
                                    <InputOTP {...field} maxLength={3} type="text">
                                        <InputOTPGroup className="flex flex-row gap-2">
                                            <InputOTPSlot
                                                className="rounded-lg px-[46px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                                index={0}
                                            />
                                            <InputOTPSlot
                                                className="rounded-lg px-[46px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                                index={1}
                                            />
                                            <InputOTPSlot
                                                className="rounded-lg px-[46px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                                index={2}
                                            />
                                        </InputOTPGroup>
                                    </InputOTP>
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />
                </div>
                <span className="text-white-blue-600">-</span>
                {/* Numeric OTP */}
                <FormField
                    control={control}
                    name="numericOtp"
                    render={({ field }) => (
                        <FormItem className="flex flex-col items-center justify-center p-2 rounded-lg border border-secondary-blue-400">
                            <FormLabel className="font-medium mx-2 text-[18px] leading-[33.73px] tracking-normal text-start">
                                الأرقام
                            </FormLabel>
                            <FormControl>
                                <InputOTP {...field} maxLength={4} type="text">
                                    <InputOTPGroup className="flex flex-row gap-2">
                                        <InputOTPSlot
                                            className="rounded-lg px-[33.5px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                            index={0}
                                        />
                                        <InputOTPSlot
                                            className="rounded-lg px-[33.5px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                            index={1}
                                        />
                                        <InputOTPSlot
                                            className="rounded-lg px-[33.5px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                            index={2}
                                        />
                                        <InputOTPSlot
                                            className="rounded-lg px-[33.5px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                            index={3}
                                        />
                                    </InputOTPGroup>
                                </InputOTP>
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <span className="text-white-blue-600">-</span>
                {/* English Letters OTP */}
                <FormField
                    control={control}
                    name="englishOtp"
                    render={({ field }) => (
                        <FormItem className="flex flex-col items-center justify-center p-2 rounded-lg border border-secondary-blue-400">
                            <FormLabel className="font-medium mx-2 text-[18px] leading-[33.73px] tracking-normal text-start">
                                الحروف الانجليزية
                            </FormLabel>
                            <FormControl>
                                <InputOTP {...field} maxLength={3} type="text">
                                    <InputOTPGroup className="flex flex-row gap-2">
                                        <InputOTPSlot
                                            className="rounded-lg px-[46px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                            index={0}
                                        />
                                        <InputOTPSlot
                                            className="rounded-lg px-[46px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                            index={1}
                                        />
                                        <InputOTPSlot
                                            className="rounded-lg px-[46px] py-3 border border-[#E8E7EA] text-base font-medium leading-5 tracking-normal text-center text-white-blue-600"
                                            index={2}
                                        />
                                    </InputOTPGroup>
                                </InputOTP>
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
            </div>
        </div>
    );
};

export default VehicleNumber;
