import UnderlineHeading from '@/components/heading/UnderlineHeading';
import ImgInputForm from '@/components/forms/ImgInputForm';
interface Props {
    media: {
        id: number;
        image: string;
        vehicle_id: null;
        images: string[];
        license: string[];
    };
}
export default function DocumentsSection({ media }: Props) {
    return (
        <div className="mt-4 mb-8">
            <UnderlineHeading text="الوثائق" className="mb-4" />

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <ImgInputForm
                    editImages={media?.image}
                    name="image"
                    maxSize={2 * 1024 * 1024}
                    multiple={false}
                    label="صور السيارة الاساسية"
                />
                <ImgInputForm
                    editImages={media?.images}
                    name="images"
                    maxSize={8 * 1024 * 1024}
                    multiple={true}
                    label="صور السيارة الدخلية"
                />
            </div>
            <ImgInputForm
                editImages={media?.license}
                name="license"
                maxSize={4 * 1024 * 1024}
                multiple={true}
                label="صور الرخصة"
            />
        </div>
    );
}
