import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import colors from '../../data/carsColors';
import TextInputForm from '@/components/forms/TextInputForm';
import { cn } from '@/lib/utils';

const CarColor = () => {
    const { control } = useFormContext();

    return (
        <div className="mt-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <FormField
                    control={control}
                    name="color"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                لون السيارة
                            </FormLabel>
                            <FormControl>
                                <Select onValueChange={field.onChange} value={field.value}>
                                    <SelectTrigger>
                                        <SelectValue placeholder="اختر لون السيارة" />
                                    </SelectTrigger>
                                    <SelectContent>
                                        <div className="grid grid-cols-[repeat(auto-fit,minmax(42px,1fr))] bg-white-blue-100 gap-4 p-2">
                                            {colors.map(({ name, value }) => (
                                                <SelectItem key={value} value={value} className="w-[36px] m-0 p-0">
                                                    <button
                                                        key={value}
                                                        type="button"
                                                        className={cn('w-10 h-[37px] rounded border-2', {
                                                            'border-black': field.value === value,
                                                            'border-transparent': field.value !== value,
                                                        })}
                                                        style={{ backgroundColor: value }}
                                                    >
                                                        {/* {name} */}
                                                    </button>
                                                </SelectItem>
                                            ))}
                                        </div>
                                    </SelectContent>
                                </Select>
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <TextInputForm
                    name="next_maintainance_mile"
                    type="number"
                    label="عدد الكيلومتر للصيانة القادمة"
                    placeholder="أدخل عدد الكيلومتر للصيانة القادمة"
                />
            </div>
        </div>
    );
};

export default CarColor;
