import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { Brand, Car, Model } from '@/types/cars.types';
import CalendarIcon from '@/components/icons/CalendarIcon';

interface Props {
    car?: Car;
}

export default function VehicleInfo({ car }: Props) {
    const { control, setValue } = useFormContext();
    const [models, setModels] = useState<Model[]>([]);
    const [brands, setBrands] = useState<Brand[]>([]);
    const [selectedBrand, setSelectedBrand] = useState<number | null>(null);
    const currentYear = new Date().getFullYear();
    const [brandError, setBrandError] = useState<string | null>(null);
    const [modelError, setModelError] = useState<string | null>(null);

    useEffect(() => {
        const fetchBrands = async () => {
            try {
                const response = await apiGet({ endpoint: endpoints.brands.list });
                setBrands(response.data.data);
                setBrandError(null);
            } catch (error) {
                setBrandError('Error fetching brands. Please try again later.');
            }
        };

        fetchBrands();
    }, []);

    useEffect(() => {
        const fetchModels = async (brandId: number) => {
            try {
                const response = await apiGet({ endpoint: `${endpoints.brands.show}/${brandId}` });
                setModels(response.data.data.models);
                setModelError(null);
            } catch (error) {
                setModelError('Error fetching models. Please try again later.');
            }
        };

        if (selectedBrand) {
            fetchModels(selectedBrand);
        } else {
            setModels([]);
            setModelError(null);
        }
    }, [selectedBrand]);

    useEffect(() => {
        if (brands.length > 0 && car?.vehicle_brand_id) {
            setValue('vehicle_brand_id', car?.vehicle_brand_id);
            handleSelectedBrand(car?.vehicle_brand_id);
        }
    }, [brands]);

    useEffect(() => {
        if (models.length > 0 && car?.vehicle_model_id) {
            setValue('vehicle_model_id', car?.vehicle_model_id);
        }
    }, [models]);

    const handleSelectedBrand = (id: number) => {
        const brand = brands.find((brand) => brand.id === id);
        if (brand) {
            setSelectedBrand(brand.id);
            if (car) {
                setValue('vehicle_model_id', car?.vehicle_model_id);
            } else {
                setValue('vehicle_model_id', undefined);
            }
            setModelError(null);
        }
    };

    return (
        <div className="mt-6 pt-4 border-t border-t-white-blue-500">
            <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
                <FormField
                    control={control}
                    name="vehicle_brand_id"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                الماركة
                            </FormLabel>
                            <Select
                                onValueChange={(value) => {
                                    field.onChange(Number(value));
                                    handleSelectedBrand(Number(value));
                                    setBrandError(null);
                                }}
                                defaultValue={field.value}
                                value={car && String(field.value)}
                            >
                                <FormControl>
                                    <SelectTrigger>
                                        <SelectValue
                                            placeholder={<span style={{ color: '#D1D1D1' }}>اختر الماركة</span>}
                                        />
                                    </SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    {brands.map((brand) => (
                                        <SelectItem key={brand.id} value={String(brand.id)}>
                                            {brand.name}
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            <FormMessage>{brandError}</FormMessage>
                        </FormItem>
                    )}
                />

                <FormField
                    control={control}
                    name="vehicle_model_id"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                الموديل
                            </FormLabel>
                            <Select
                                onValueChange={(value) => field.onChange(Number(value))}
                                defaultValue={field.value}
                                disabled={!selectedBrand}
                                value={car && String(field.value)}
                            >
                                <FormControl>
                                    <SelectTrigger>
                                        <SelectValue
                                            placeholder={<span style={{ color: '#D1D1D1' }}>اختر الموديل</span>}
                                        />
                                    </SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    {models.map((model) => (
                                        <SelectItem key={model.id} value={String(model.id)}>
                                            {model.name}
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            <FormMessage>{modelError}</FormMessage>
                        </FormItem>
                    )}
                />

                <FormField
                    control={control}
                    name="make_year"
                    render={({ field }) => (
                        <FormItem className="flex flex-col justify-start">
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                سنة الصنع
                            </FormLabel>
                            <Select
                                onValueChange={(value) => field.onChange(Number(value))}
                                value={car && String(field.value)}
                            >
                                <FormControl>
                                    <SelectTrigger
                                        className="w-full text-black"
                                        Icon={<CalendarIcon width="24" height="24" className="text-white-blue-600" />}
                                    >
                                        <SelectValue
                                            placeholder={<span style={{ color: '#D1D1D1' }}>أدخل سنة الصنع</span>}
                                        />
                                    </SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    {Array.from(
                                        { length: currentYear - (currentYear - 5) },
                                        (_, i) => currentYear - 4 + i,
                                    ).map((y) => (
                                        <SelectItem key={y} value={String(y)}>
                                            {y}
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            <FormMessage />
                        </FormItem>
                    )}
                />
            </div>
        </div>
    );
}
