import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Button } from '@/components/ui/button';
import { format } from 'date-fns';
import CalendarIcon from '@/components/icons/CalendarIcon';

export default function InsuranceDetails() {
    const { control } = useFormContext();

    return (
        <div className="mt-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <FormField
                    control={control}
                    name="insurance_type"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                نوع التامين
                            </FormLabel>
                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                                <FormControl>
                                    <SelectTrigger>
                                        <SelectValue
                                            placeholder={<span style={{ color: '#D1D1D1' }}>اختر نوع التأمين</span>}
                                        />
                                    </SelectTrigger>
                                </FormControl>
                                <SelectContent>
                                    <SelectItem value="comprehensive">شامل</SelectItem>
                                    <SelectItem value="thirdParty">ضد الغير</SelectItem>
                                </SelectContent>
                            </Select>
                            <FormMessage />
                        </FormItem>
                    )}
                />

                <FormField
                    control={control}
                    name="insurance_expiry_date"
                    render={({ field }) => (
                        <FormItem className="flex flex-col">
                            <FormLabel className="text-primery-blue-900 font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                                تاريخ انتهاء التامين
                            </FormLabel>
                            <Popover>
                                <PopoverTrigger asChild>
                                    <FormControl>
                                        <Button
                                            variant="outline"
                                            className="pe-3 text-start font-medium flex flex-row justify-between"
                                        >
                                            {field.value ? (
                                                format(field.value, 'yyyy-MM-dd')
                                            ) : (
                                                <span className="text-white-blue-600">أدخل تاريخ انتهاء التامين</span>
                                            )}
                                            <CalendarIcon width="24" height="24" className="text-white-blue-600" />
                                        </Button>
                                    </FormControl>
                                </PopoverTrigger>
                                <PopoverContent className="w-auto p-0" align="start">
                                    <Calendar
                                        mode="single"
                                        selected={field.value}
                                        onSelect={field.onChange}
                                        disabled={(date) => date < new Date()}
                                        initialFocus
                                    />
                                </PopoverContent>
                            </Popover>
                            <FormMessage />
                        </FormItem>
                    )}
                />
            </div>
        </div>
    );
}
